import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-page4',
  templateUrl: './page4.component.html',
  styleUrls: ['./page4.component.scss']
})
export class Page4Component implements OnInit {

  name: string;

  fileList = [
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
    {name: ''},
  ];

  constructor() { }

  ngOnInit(): void {
  }

  onSend(): void {
    let count = 0;
    this.fileList.forEach((item)=>{
      if(item.name){
        count++;
      }
    })
    if (!this.name) {
      alert('作品名稱未輸入');
    } else if(count < 3) {
      alert('檔案數量小於3');
    } else {
      alert('上傳成功');
      this.onClear();
    }
  }

  onClear(): void {
    this.name = '';
    this.fileList.forEach((item)=>{
      if(item.name){
        item.name = '';
      }
    })
  }

}
